import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';

import LineGraph from '../../components/lineGraph';

@withText((props) => ({
    week: <Text id='workout.graph.week' />,
    distanceAvgAll: <Text id='workout.graph.distance-avg-all' />,
    distanceAvg: <Text id='workout.graph.distance-avg' />,
    ascendAvgAll: <Text id='workout.graph.ascend-avg-all' />,
    ascendAvg: <Text id='workout.graph.ascend-avg' />,
    speedAvgAll: <Text id='workout.graph.speed-avg-all' />,
    speedAvg: <Text id='workout.graph.speed-avg' />,
    durationAvgAll: <Text id='workout.graph.duration-avg-all' />,
    durationAvg: <Text id='workout.graph.duration-avg' />,
    realDurationAvgAll: <Text id='workout.graph.real-duration-avg-all' />,
    realDurationAvg: <Text id='workout.graph.real-duration-avg' />,
    loadIndex: <Text id='workout.graph.load-index' />,
    loadIndexAll: <Text id='workout.graph.load-index-all' />,
    plan: <Text id='workout.graph.plan' />,
}))
@observer
class WorkoutGraphAvg extends Component {
    render() {
        const { userStore } = this.props.stores;
        const { week, distanceAvgAll, distanceAvg, ascendAvgAll, ascendAvg, speedAvgAll, speedAvg,
            durationAvg, realDurationAvg,
            durationAvgAll, realDurationAvgAll,
            loadIndex, loadIndexAll,
            year,
            teamAmbition,
            teamType,
            teamId,
        } = this.props;
        const {
			graphAvgWorkoutStatsDistance,
			graphAvgWorkoutStatsElevation,
            graphAvgWorkoutStatsSpeed,
            graphAvgWorkoutStatsDuration,
            graphAvgWorkoutStatsRealDuration,
            graphAvgWorkoutStatsLoadIndex,
            graphWorkoutAvgWeekDistanceCurrent,
            graphWorkoutAvgWeekElevationCurrent,
            graphWorkoutAvgWeekSpeedCurrent,
            graphWorkoutAvgWeekDurationCurrent,
            graphWorkoutAvgWeekRealDurationCurrent,
            graphWorkoutAvgWeekLoadIndexCurrent,
            graphAvgWorkoutStatsCount,
            user,
		} = userStore;

        const { settings = {} } = user;

        const currentMonth = new Date().getMonth() + 1;
        const currentYear = year || util.getYear();
		const seasonStart = parseInt(currentMonth < 8 ? currentYear - 1 : currentYear, 10);

        // const summaryMonth = workoutPlanStore.findWorkoutPlanSummaryMonth(year, monthNum) || {};
        const seasonWeeks = util.weekRange(`${seasonStart}-08-01`, `${seasonStart + 1}-07-31`);

        const xTicksAll = seasonWeeks.map(wo => wo.week);
        const parts = xTicksAll.length % 6;
        const partSize = Math.floor(xTicksAll.length / parts);
        const xTicks = [];
        for (let i = 0, l = xTicksAll.length; i < l; i += partSize) {
            xTicks.push(xTicksAll[i]);
        }

        return (
            <>
                {teamType && <a href={`/settings/team/${teamId}`}><span class='badge badge-pill badge-secondary ml-2 float-right'>{teamType}</span></a>}
                {teamAmbition && <a href={`/settings/team/${teamId}`}><span class='badge badge-pill badge-secondary ml-2 float-right'>{teamAmbition}</span></a>}

                <h5><Text id='workout.graph.title-avg'>Gjennomsnitt</Text> {seasonStart} - {seasonStart + 1}</h5>
                <div class='w-100 mt-3 mb-4'>
                    {/* <xmp>graphWorkoutSummaryWeekDistanceCurrent: {JSON.stringify(graphWorkoutSummaryWeekDistanceCurrent, null, 4)}</xmp>
                    <xmp>graphWorkoutSummaryWeekDistancePrev: {JSON.stringify(graphWorkoutSummaryWeekDistancePrev, null, 4)}</xmp> */}
                    <LineGraph
                        stores={this.props.stores}
                        width={500}
                        height={150}
                        showXTicks={1}
                        xTicksVal={val => `${week} ${val}`}
                        xTicks={xTicks}
                        paddingTop={30}
                        lineFromXzero={true}
                        lineFromXmax={true}

                        dataLeft={[graphWorkoutAvgWeekLoadIndexCurrent, graphAvgWorkoutStatsLoadIndex]}
                        legendLeft={[`${seasonStart} ${loadIndex}`, `${seasonStart} ${loadIndexAll}`]}
                        dataFillLeft={[1]}

                        // yMaxLeft={30}
                        // yMinLeft={0}
                        showYTicksLeft={1}
                        avgWindowLeft={[0, 2]}
                        smoothLeft={[1, 1]}

                        dataRight={[graphAvgWorkoutStatsCount]}
                        showYTicksRight={1}
                        smoothRight={[1]}
                        // yTicksRightPostfix={'m'}
                        // unitRight={['m', 'm']}
                        // avgWindowRight={[0, 0]}

                        // marker={week}
                    />
                </div>

                <div class='w-100 mt-3 mb-4'>
                    {/* <xmp>graphWorkoutSummaryWeekDistanceCurrent: {JSON.stringify(graphWorkoutSummaryWeekDistanceCurrent, null, 4)}</xmp>
                    <xmp>graphWorkoutSummaryWeekDistancePrev: {JSON.stringify(graphWorkoutSummaryWeekDistancePrev, null, 4)}</xmp> */}
                    <LineGraph
                        stores={this.props.stores}
                        width={500}
                        height={150}
                        showXTicks={1}
                        xTicksVal={val => `${week} ${val}`}
                        xTicks={xTicks}
                        paddingTop={30}
                        lineFromXzero={true}
                        lineFromXmax={true}

                        dataLeft={[graphWorkoutAvgWeekDistanceCurrent, graphAvgWorkoutStatsDistance]}
                        legendLeft={[`${seasonStart} ${distanceAvg}`, `${seasonStart} ${distanceAvgAll}`]}
                        dataFillLeft={[1]}
                        // yMaxLeft={30}
                        // yMinLeft={0}
                        showYTicksLeft={1}
                        yTicksLeftPostfix={settings.imperial ? 'mi' : 'km'}
                        unitLeft={settings.imperial ? ['mi', 'mi', 'mi'] : ['km', 'km', 'km']}
                        avgWindowLeft={[0, 2]}
                        smoothLeft={[1, 1]}

                        dataRight={[graphAvgWorkoutStatsCount]}
                        showYTicksRight={1}
                        smoothRight={[1]}
                        // yTicksRightPostfix={'m'}
                        // unitRight={['m', 'm']}
                        // avgWindowRight={[0, 0]}
                        // smoothRight={[1, 1]}

                        // marker={week}
                    />
                </div>
                <div class='w-100 mt-3 mb-4'>
                    {/* <xmp>graphWorkoutSummaryWeekDistanceCurrent: {JSON.stringify(graphWorkoutSummaryWeekDistanceCurrent, null, 4)}</xmp>
                    <xmp>graphWorkoutSummaryWeekDistancePrev: {JSON.stringify(graphWorkoutSummaryWeekDistancePrev, null, 4)}</xmp> */}
                    <LineGraph
                        stores={this.props.stores}
                        width={500}
                        height={150}
                        showXTicks={1}
                        xTicksVal={val => `${week} ${val}`}
                        xTicks={xTicks}
                        paddingTop={30}
                        lineFromXzero={true}
                        lineFromXmax={true}

                        dataLeft={[graphWorkoutAvgWeekElevationCurrent, graphAvgWorkoutStatsElevation]}
                        legendLeft={[`${seasonStart} ${ascendAvg}`, `${seasonStart} ${ascendAvgAll}`]}
                        dataFillLeft={[1]}
                        // yMaxLeft={30}
                        // yMinLeft={0}
                        showYTicksLeft={1}
                        yTicksLeftPostfix={settings.imperial ? 'ft' : 'm'}
                        unitLeft={settings.imperial ? ['ft', 'ft', 'ft'] : ['m', 'm', 'm']}
                        avgWindowLeft={[0, 2]}
                        smoothLeft={[1, 1]}

                        dataRight={[graphAvgWorkoutStatsCount]}
                        showYTicksRight={1}
                        smoothRight={[1]}
                        // marker={week}
                    />
                </div>
                <div class='w-100 mt-3 mb-4'>
                    {/* <xmp>graphWorkoutSummaryWeekDistanceCurrent: {JSON.stringify(graphWorkoutSummaryWeekDistanceCurrent, null, 4)}</xmp>
                    <xmp>graphWorkoutSummaryWeekDistancePrev: {JSON.stringify(graphWorkoutSummaryWeekDistancePrev, null, 4)}</xmp> */}
                    <LineGraph
                        stores={this.props.stores}
                        width={500}
                        height={150}
                        showXTicks={1}
                        xTicksVal={val => `${week} ${val}`}
                        xTicks={xTicks}
                        paddingTop={30}
                        lineFromXzero={true}
                        lineFromXmax={true}

                        dataLeft={[graphWorkoutAvgWeekSpeedCurrent, graphAvgWorkoutStatsSpeed]}
                        legendLeft={[`${seasonStart} ${speedAvg}`, `${seasonStart} ${speedAvgAll}`]}
                        dataFillLeft={[1]}
                        // yMaxLeft={30}
                        // yMinLeft={0}
                        showYTicksLeft={1}
                        yTicksLeftPostfix={settings.imperial ? 'mph' : 'km/t'}
                        avgWindowLeft={[0, 2]}
                        smoothLeft={[1, 1]}

                        dataRight={[graphAvgWorkoutStatsCount]}
                        showYTicksRight={1}
                        smoothRight={[1]}

                        // dataRight={[
                        //     graphWorkoutAvgWeekDurationCurrent,
                        //     graphAvgWorkoutStatsDuration,
                        // ]}
                        // legendRight={[
                        //     `${seasonStart} ${durationAvg}`,
                        //     `${seasonStart} ${durationAvgAll}`,
                        // ]}
                        // showYTicksRight={1}
                        // yTicksRightVal={val => util.secToHms(val, true)}
                        // avgWindowRight={[0, 0, 2, 2]}
                        // smoothRight={[1, 1, 1, 1]}
                        // marker={week}
                    />
                </div>
                <div class='w-100 mt-3 mb-4'>
                    {/* <xmp>graphWorkoutSummaryWeekDistanceCurrent: {JSON.stringify(graphWorkoutSummaryWeekDistanceCurrent, null, 4)}</xmp>
                    <xmp>graphWorkoutSummaryWeekDistancePrev: {JSON.stringify(graphWorkoutSummaryWeekDistancePrev, null, 4)}</xmp> */}
                    <LineGraph
                        stores={this.props.stores}
                        width={500}
                        height={150}
                        showXTicks={1}
                        xTicksVal={val => `${week} ${val}`}
                        xTicks={xTicks}
                        paddingTop={30}
                        lineFromXzero={true}
                        lineFromXmax={true}

                        dataLeft={[graphWorkoutAvgWeekDurationCurrent, graphAvgWorkoutStatsDuration]}
                        legendLeft={[`${seasonStart} ${durationAvg}`, `${seasonStart} ${durationAvgAll}`]}
                        dataFillLeft={[1]}
                        // yMaxLeft={30}
                        // yMinLeft={0}
                        showYTicksLeft={1}
                        yTicksLeftVal={val => util.secToHms(val, true)}
                        avgWindowLeft={[0, 2]}
                        smoothLeft={[1, 1]}

                        dataRight={[graphAvgWorkoutStatsCount]}
                        showYTicksRight={1}
                        smoothRight={[1]}
                        // marker={week}
                    />
                </div>
            </>
        );
    }
}

export default WorkoutGraphAvg;
